import { Outlet } from 'react-router-dom';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import NotificationsNoneIcon from '@mui/icons-material/NotificationsNone';
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';
import LogoutOutlinedIcon from '@mui/icons-material/LogoutOutlined';
import {
  ContainerUserMain,
  PagesForSidebar,
  Sidebar,
} from './UserPageComponent.styled';
import SidebarListComponent from './SidebarListComponent';
import { useDispatch } from 'react-redux';
import { logOut } from '../../redux/auth/thunk';

export default function UserPageMain() {
  const dispatch = useDispatch();
  function onExit() {
    dispatch(logOut());
  }
  return (
    <ContainerUserMain>
      <Sidebar>
        <SidebarListComponent nameList=" " path={'my_order'}>
          <AddShoppingCartIcon />
        </SidebarListComponent>
        <SidebarListComponent nameList="  " path={'my_poster'}>
          <NotificationsNoneIcon />
        </SidebarListComponent>
        <SidebarListComponent nameList="" path={'notification'}>
          <ChatBubbleOutlineIcon />
        </SidebarListComponent>
        <SidebarListComponent nameList="" path={'selected'}>
          <FavoriteBorderIcon />
        </SidebarListComponent>
        <SidebarListComponent nameList="" path={'profile'}>
          <PersonOutlineOutlinedIcon />
        </SidebarListComponent>
        <SidebarListComponent nameList="" path={'/'} onClick={onExit}>
          <LogoutOutlinedIcon />
        </SidebarListComponent>
      </Sidebar>
      <PagesForSidebar>
        <Outlet />
      </PagesForSidebar>
    </ContainerUserMain>
  );
}
